.item-details {
  display: flex;
  flex-direction: row;
  padding: 1rem;
}
  
.item-details .item-image {
  width: 30%;
  height: 30%;
  border-radius: 10px;
}
  
.item-details .list-group-item {
  padding: .25rem;
}
  
.item-details .list-group-item .element{
  margin-right: .5rem;
}
.btn-back {
  cursor: pointer;
  border: none;
  background: #397794;
  color: #fff;
  padding:3px 10px;
  text-transform: uppercase;
  border-radius: 10px;
  position: absolute;
  right:30px;
  top:15px;
  font-size: .7rem;
  transition: .2s all linear;
}
.btn-back:active,.btn-back:focus,.btn-back:hover {
 background-color: #da6e0e
}
@media(max-width:550px) {
  .item-details {
    flex-direction: column-reverse;
  }
  .card-body {
    padding-left:0;
  }
  .item-details .item-image {
    width:100%;
    height: auto;
  }
  .btn-back {
    left:10px;
    top:10px;
  }
}