video.video { 
    position: fixed;
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    z-index: -100;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
@media(max-width:550px) {
  .home-wrapper {
    order:1;
  }
  .home-wrapper h2 {
    font-size:1.5rem; 
  }
  video.video {
    position: inherit;
    top:0;
    left:0;
    width:100%;
    height:auto;
    -webkit-transform: none;
            transform: none;
    border-radius: .2rem;
  }
}
